<template>
  <v-container class="fill-height" fluid>
    <v-row>
      <v-col :cols="12">
        <v-container v-if="!logged">
          <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="4">
              <v-form>
                <v-text-field
                  v-model="name"
                  :counter="10"
                  label="Name"
                  required
                ></v-text-field>
                <v-text-field
                  v-model="room"
                  :counter="10"
                  label="Room"
                  required
                ></v-text-field>
                <v-btn color="green" class="mr-4" @click="createUser">
                  Go
                </v-btn>
              </v-form>
            </v-col>
          </v-row>
        </v-container>
        <v-container v-else>
          <v-row class="px-2">
            <v-col>
              <p>Current users: {{ users.length }} users</p>
              <v-card-text v-for="user in users" :key="user.id">
                <div>Name</div>
                <p class="text-h4 text--primary">
                  {{ user.name }}
                  <span v-if="user.id == socket.id" class="text-center">
                    (You)
                    <v-btn align="right" @click="exitRoom"> Exit </v-btn>
                  </span>
                </p>
              </v-card-text>
            </v-col>
          </v-row>
          <v-row class="px-2">
            <v-col>
              <Editor
                ref="editor"
                v-model="code"
                @input="socketUpdateText"
                @newCursor="socketUpdateCursor"
              />
            </v-col>
          </v-row>
        </v-container>
        <v-snackbar v-model="snackbar" :timeout="timeout">
          {{ notiContent }}
        </v-snackbar>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
const TextController = require("../util/controller");
import io from "socket.io-client";
import Editor from "../components/Editor";
const ENDPOINT = "https://diffsyncalter-server.herokuapp.com/";
// const ENDPOINT = "http://localhost:3017";
export default {
  data() {
    return {
      logged: false,
      value: {
        type: String,
        required: false,
      },
      connected: false,
      socket: {},
      intel: 1,
      name: "",
      room: "",
      snackbar: false,
      timeout: 3000,
      notiContent: "",
      users: [],
      code: "",
      clp: "",
      tc: undefined,
      resetPass: 0,
      resetTextPass: 0,
    };
  },
  components: {
    Editor,
  },
  mounted() {},
  methods: {
    createUser() {
      if (this.name === "" || this.room === "") {
        return;
      }
      this.initializeSocket();
      this.logged = true;
      console.log(this.name, this.room);
      let name = this.name;
      let room = this.room;
      this.socket.emit("join", {
        name,
        room,
      });
    },
    exitRoom() {
      this.logged = false;
      this.code = "";
      this.socket.close();
    },
    initializeSocket() {
      this.tc = new TextController();
      this.socket = io(ENDPOINT, {
        transports: ["websocket", "polling", "flashsocket"],
      });
      this.socket.on("notification", (data) => {
        this.notiContent = data.text;
        this.snackbar = true;
      });
      this.socket.on("roomData", (data) => {
        this.users = data.users;
      });
      this.socket.on("text", ({ patch, shadow }) => {
        let changes = this.tc.applyPatch(patch);
        this.shadow = shadow;
        if (changes) {
          // console.log("supposed to be at ", this.tc.setCursor());
          this.code = this.tc.code;
          this.$refs.editor.modifyCursor(this.tc.setCursor());
          this.resetPass++;
          this.resetTextPass++;
        }
      });
    },
    socketUpdateCursor(data) {
      if (data.line == 0 && data.ch == 0 && this.resetPass) {
        this.resetPass--;
        this.$refs.editor.modifyCursor(this.tc.setCursor());
      } else {
        this.tc.updateCursor(data.line, data.ch);
      }
    },
    socketUpdateText(data) {
      let patch = this.tc.makePatch(data);
      this.tc.localPatch(patch);
      this.socket.emit("sendText", patch);
    },
  },
};
</script>

<style></style>
