<template>
  <v-row>
    <v-col class="px-0">
      <v-card outlined>
        <v-row>
          <v-col class="py-0 mt-1" >
            <codemirror
              ref="myCm"
              :value="value"
              @ready="onCmReady"
              @input="onCodeChange"
              :options="cmOptions"
              :class="fontSize"
            ></codemirror>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import { codemirror, CodeMirror } from "vue-codemirror";
import "codemirror/lib/codemirror";

import "codemirror/addon/comment/comment.js";
import 'codemirror/addon/hint/show-hint';
import 'codemirror/addon/search/searchcursor';
import 'codemirror/addon/search/search';
import 'codemirror/addon/display/placeholder';
import 'codemirror/addon/hint/show-hint.css';
import 'codemirror/addon/edit/closebrackets';

import 'codemirror/lib/codemirror.css'

// import theme style
import '../assets/css/codemirror.css'

export default {
  data() {
    return {
      autoCloseBrackets: true,
      tabSize: 4,
      theme: "default",
      language: "default",
      keyMap: "default",
      fontSize: "normal",
    };
  },
  props: {
    value: {
      type: String,
      required: false
    },
  },
  components: {
    codemirror
  },
  computed: {
    codemirror() {
      return this.$refs.myCm.codemirror;
    },
    cmOptions() {
      return {
        tabSize: this.tabSize,
        indentUnit: this.tabSize,
        indentWithTabs: true,
				smartIndent: true,
        autoCloseBrackets: this.autoCloseBrackets,
        lineNumbers: true,
        line: true,
        styleActiveLine: true,
        matchBrackets: true,
        lineWrapping: true,
        theme: this.theme,
        foldGutter: true,
        keyMap: this.keyMap,
        extraKeys: {
          'Ctrl-/': 'toggleComment',
          'Cmd-/' : 'toggleComment',
          // 'Enter': this.onNewLine,
        }
      };
    },
  },
  methods: {
    onCmReady(cm) {
      console.log("Codemirror is ready !");
      cm.on('cursorActivity', (cm) => {
        let data = cm.getCursor();
        this.$emit('newCursor', {
          line: data.line,
          ch: data.ch
        });
      });
    },
    onCodeChange(newValue) {
      this.$emit('input', newValue);
    },
    modifyCursor: function(data) {
      console.log(data);
      this.codemirror.setCursor(data);
    },
    onNewLine(cm) {
      var doc = cm.getDoc();
      var cursor = doc.getCursor(); // gets the line number in the cursor position
      var line = doc.getLine(cursor.line); // get the line contents
      var pos = { // create a new object to avoid mutation of the original selection
        line: cursor.line,
        ch: line.length // set the character position to the end of the line
      }
      doc.replaceRange('\n', pos); // adds a new line
      return CodeMirror.pass;
    }
  },
};

</script>