const DiffPatchMatch = require("diff-match-patch");

const dmp = new DiffPatchMatch();

class TextController {
  constructor(code = "") {
    this.code = code;
    this.shadow = code;
    this.cursorPos = 0;
  }
  applyPatch(patches) {
    let result = dmp.patch_apply(patches, this.code);
    this.code = result[0];
    // this.shadow = this.code;
    let patch_applied = 0;
    for (let i = 0; i < result[1].length; i++) {
      if (result[1][i]) {
        patch_applied++;
      }
    }
    let iter = 0;
    let delta = 0;
    for (let i = 0; i < patches.length; i++) {
      if (result[1][i]) {
        if (patches[i].start1 !== patches[i].start2) {
          console.log("Error at patch ", i);
        }
        // patch is applying to [start1, start1 + length1)
        if (patches[i].start1 + patches[i].length1 <= this.cursorPos)
          delta += patches[i].length2 - patches[i].length1;
        else if (patches[i].start1 >= this.cursorPos) break;
        else {
          iter = patches[i].start1;
          for (let j = 0; j < patches[i].diffs.length; j++) {
            if (patches[i].diffs[j][0] == 0) {
              iter += patches[i].diffs[j][1].length;
            } else if (patches[i].diffs[j][0] == 1) {
              if (iter <= this.cursorPos) {
                delta += patches[i].diffs[j][1].length;
              }
            } else {
              iter += patches[i].diffs[j][1].length;
              if (iter <= this.cursorPos) {
                delta -= patches[i].diffs[j][1].length;
              }
            }
          }
        }
      }
    }
    this.cursorPos += delta;
    if (this.cursorPos > this.code.length) this.cursorPos = this.code.length;
    // console.log("cursor delta", delta);
    return patch_applied;
  }
  updateCursor(line, ch) {
    let iter = 0;
    let lineCount = 0;
    while (lineCount < line) {
      if (this.code[iter] == "\n") {
        lineCount++;
      }
      iter++;
    }
    iter += ch;
    this.cursorPos = iter;
    // console.log("updated to ", this.cursorPos, "from", line, ch);
  }
  setCursor() {
    let lastEndlineFound = -1;
    let lineCount = 0;
    for (let i = this.cursorPos - 1; i >= 0; i--) {
      if (this.code[i] === "\n") {
        lineCount++;
        if (lastEndlineFound == -1) {
          lastEndlineFound = i;
        }
      }
    }
    return {
      line: lineCount,
      ch: this.cursorPos - 1 - lastEndlineFound,
    };
  }
  makePatch(data = undefined) {
    if (data === undefined) {
      return dmp.patch_make(this.shadow, this.code);
    } else {
      return dmp.patch_make(this.code, data);
    }
  }
  fullPatch() {
    return dmp.patch_make("", this.code);
  }
  displayPatch() {
    return dmp.patch_toText(this.makePatch());
  }
  localPatch(patch) {
    let result = dmp.patch_apply(patch, this.code);
    this.code = result[0];
    result = dmp.patch_apply(patch, this.shadow);
    this.shadow = result[0];
  }
}

module.exports = TextController;
